.menu-mob {

  &.menu-mob--closed {
    display: none;
  }

  &.menu-mob--opened {
    display: flex;
  }

  @media (max-width: $tablet-width) {
    position: fixed;
    top:0;
    left:0;
    right:0;
    bottom:0;
    display: flex;
    height:100%;
    background: #fff;
    z-index: 20;
  }
}

.menu-mob--nojs {

  @media (max-width:$tablet-width) {
    position: absolute;
    height:150px;
    width:100%;
    top:50px;
    z-index: 0;

    .menu-list {
      margin:0;
    }

    &.menu-mob--closed {
      display: flex;
    }
  }
}
