.contacts {
  display: flex;
  flex-direction: column;
  align-items: center;
  width:100%;
  padding:0 0 53px 0;

  @media (max-width: $tablet-width) {
    padding:0 0 53px 0;
  }

  @media (max-width: $mobile-width-only) {
    padding:0 0 51px 0;
  }

  p {
    width:555px;
    font-size: 17px;
    line-height: 31px;
    text-align: center;
    color: rgba(0, 0, 0, 0.7);
    margin:30px 0 0 0;

    @media (max-width: $tablet-width) {
      width:629px;
      margin:20px 0 0 0;
    }

    @media (max-width: $mobile-width-only) {
      width:268px;
    }
  }
}
.contacts__name {
  margin:248px 0 0 0;
  font-weight: 600;
  font-size: 30px;
  line-height: 30px;
  letter-spacing: -0.005em;
  color: $dark;

  @media (max-width: $tablet-width) {
    font-size: 24px;
    margin:155px 0 0 0;
  }

  @media (max-width: $mobile-width-only) {
    font-size: 18px;
    margin:119px 0 0 0;
  }
}
